#skeleton .card {
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  padding: 8px;
  margin: 8px 0px;
}
#skeleton .card-header {
  display: flex;
  align-items: center;
}
#skeleton .card-avatar {
  margin: 8px;
  flex: 0 0 auto;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: #ccc;
}
#skeleton .header-texts {
  margin: 8px;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: space-around;
  align-self: stretch;
  display: flex;
}
#skeleton .header-text-line1 {
  height: 10px;
  width: 40%;
  background-color: #ccc;
  border-radius: 4px;
}
#skeleton .header-text-line2 {
  height: 10px;
  width: 20%;
  background-color: #ccc;
  border-radius: 4px;
}

#skeleton .content-line1 {
  height: 10px;
  margin: 8px;
  background-color: #ccc;
  border-radius: 4px;
}
#skeleton .content-line2 {
  height: 10px;
  margin: 8px;
  width: 60%;
  background-color: #ccc;
  border-radius: 4px;
}
#skeleton .bottom {
  height: 30px;
  margin: 8px;
  margin-top: 30px;
  background-color: #ccc;
  border-radius: 4px;
}


#skeleton .top-tabs-menu {
  padding: 8px;
  margin: 8px 0px;
  display: flex;
  justify-content: center;
}
#skeleton .top-tabs-menu-item {
  height: 30px;
  width: 150px;
  margin: 0px 8px;
  background-color: #ccc;
}

.xanimate {
  height: 38px;
  background-color: #ccc;
}